<style lang="scss">
.social-links {
  display: flex;
  justify-content: center;
  align-items: center;
  &__item:not(:last-child) {
    padding-right: 35px;
  }
  .social-link {
    display: flex;
    align-items: center;
    
    &__img {
      margin-right: 8px;
      fill: $opacity;
      transition: all ease .2s;
      padding-bottom: 1px;
    }
    &__text {
      font-weight: 500;
      font-size: 14px;
      line-height: 17px;
      color: $opacity;
      transition: all ease .2s;
    }
  }
  .social-link:hover .social-link__text {
    color: $no-opacity;
  }
  .social-link:hover .social-link__img {
    fill: $no-opacity;
  }
}
</style>